.sidebar {
  overflow: hidden;

  .sidebar-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 3.5rem;

    &__link {
      cursor: pointer;
    }

    & ul {
      padding: 0;
    }

    & li {
      margin-top: 2.2rem;
      list-style: none;
    }
  }

  &__format {
    background: $dark-grey;
    border-radius: 1.2rem 0 0 1.2rem;
    color: $white;
    margin-right: -150%;
    min-height: 100vh;
    position: fixed;
    right: 0;
    top: 0;
    transition: all 0.5s;
    width: 50%;
    z-index: 1000;
  }

  &__is-open {
    margin-right: 0;

    @include desktop {
      margin-right: -150%;
    }
  }

  &__overlay {
    background-color: hsla(234, 9%, 22%, 0.384);
    overflow: hidden;
    height: 120%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
  }

  &__is-open-overlay {
    opacity: 3;

    @include desktop {
      display: none;
    }
  }
}
